#inLine{
   font-size: 60px;
   font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
   color: rgb(160, 236, 160);

}
#btn{
    color: rgb(144, 227, 144);
    padding: 10px;
    font-size: 30px;
    border: 0px;
    border-radius:20px;
    background-color:rgb(190, 104, 104);
}
#btn:hover{
    background-color: rgb(234, 234, 127);
}
#according{
  margin-top: 40px;
  z-index: 1000;
  background: url(https://img.freepik.com/free-vector/cartoon-web-design-background_52683-70879.jpg?w=2000);
  background-size: cover;
  margin-bottom: 100px;
}

@media screen and (max-width: 480px) {
    #inLine{
    font-size: small;
    }
    #home{
        display: none;

    }
    #btn{
        padding: 10px;
       font-size: small;
     margin-left: auto;
    }
    
 }